@use "base/variables" as v;
@use "base/mixins" as m;

.pasos {
  margin-top: 5rem;

  &__contenido {
    background-color: v.$primario;
    margin-top: -1px;
  }
  &__heading {
    text-align: center;
    color: v.$blanco;
    &--descripcion {
      font-size: 1.8rem;
      display: block;
    }
  }
  &__grid {
    @include m.tablet {
      @include m.grid(3, v.$separacion);
    }
  }
  &__imagen {
    transition: 0.3s;
    &:hover {
      transform: rotate(-4deg) scale(1.1);
      transition: 0.3s;
    }
  }
}
