html {
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  /* 1 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 2 */
}

pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 5rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .navegacion {
    width: auto;
    flex-direction: row;
  }
}
.navegacion__link {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
}
.navegacion__link--registrar {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.navegacion__link--registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navegacion__link--registrar {
    width: auto;
  }
}

@media (min-width: 768px) {
  .formulario__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario__contenido {
  background-color: #e8505b;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.formulario__heading {
  color: #FFFFFF;
  text-align: center;
}
.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__label {
  display: block;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-bottom: 1rem;
}
.formulario__input {
  width: 100%;
  padding: 1.5rem;
  border: none;
}
.formulario__submit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}
.formulario__imagen-contenedor {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.formulario__imagen {
  width: 30rem;
}
@media (min-width: 1024px) {
  .formulario__imagen {
    width: 100%;
  }
}

.pasos {
  margin-top: 5rem;
}
.pasos__contenido {
  background-color: #e8505b;
  margin-top: -1px;
}
.pasos__heading {
  text-align: center;
  color: #FFFFFF;
}
.pasos__heading--descripcion {
  font-size: 1.8rem;
  display: block;
}
@media (min-width: 768px) {
  .pasos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.pasos__imagen {
  transition: 0.3s;
}
.pasos__imagen:hover {
  transform: rotate(-4deg) scale(1.1);
  transition: 0.3s;
}

.testimoniales {
  padding: 5rem 0;
}
.testimoniales__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.testimonial {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #FFFFFF;
}
.testimonial:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .testimonial {
    margin-bottom: 0;
  }
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/5;
  }
}
.testimonial__header {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.testimonial__imagen {
  flex-basis: 10rem;
  border-radius: 5rem;
  overflow: hidden;
}
.testimonial__autor {
  color: #FFFFFF;
  font-weight: bold;
  margin: 0.5rem 0;
}
.testimonial__texto {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
  padding-left: 2rem;
  position: relative;
}
.testimonial__texto::before {
  content: '"';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -5.5rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.favorito {
  background-color: #FFFFFF;
  padding: 4rem;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 1.6rem -0.8rem #4d575c;
  border-radius: 1rem;
}
.favorito:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .favorito {
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .favorito__grid {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .favorito__grid {
    grid-template-columns: 1fr 3fr;
    gap: 4rem;
  }
}
.favorito__imagen {
  max-width: 20rem;
  margin: 0 auto 2rem auto;
}
.favorito__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .favorito__contenido {
    align-items: flex-start;
  }
}
.favorito__nombre {
  margin: 1rem 0;
  color: #e8505b;
  font-size: 2.4rem;
}
.favorito__descripcion {
  margin: 0.5rem 0;
  text-align: center;
  font-size: 1.6rem;
  color: #455a64;
  line-height: 1.7;
}
@media (min-width: 768px) {
  .favorito__descripcion {
    text-align: left;
  }
}

.favoritos {
  background-color: #eceeef;
  padding: 5rem 0;
}
.favoritos__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .favoritos__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.repartidores {
  padding: 5rem 0;
}
.repartidores__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .repartidores__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.repartidores__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.repartidores__texto {
  line-height: 1.5;
  color: #455a64;
}
.repartidores__enlace {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.repartidores__enlace:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .repartidores__enlace {
    width: auto;
  }
}

.tiendas {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tiendas__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.tiendas__heading, .tiendas__texto {
  text-align: center;
}
.tiendas__heading {
  color: #455a64;
  margin: 2rem 0 0 0;
}
.tiendas__texto {
  margin-bottom: 3rem;
}
.tiendas__enlace {
  transition: 0.2s;
}
.tiendas__enlace:hover {
  transform: scale(1.1);
  transition: 0.2s;
}

.descargar {
  background-color: #eceeef;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .descargar__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.descargar__imagen {
  width: 50%;
  margin: 5rem auto 0 auto;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .descargar__imagen {
    margin-top: 0;
  }
}
.descargar__imagen:hover {
  transform: rotate(3deg) scale(1.1);
  transition: 0.3s;
}

.footer {
  background-color: #455a64;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__widget {
  margin-bottom: 5rem;
}
.footer__widget:last-of-type {
  margin-bottom: 0;
}
.footer__heading {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__heading {
    text-align: left;
  }
}
.footer__nav {
  display: flex;
  flex-direction: column;
}
.footer__nav, .footer__texto {
  margin-top: 2rem;
}
.footer__link, .footer__texto {
  font-size: 2rem;
  line-height: 1.8;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__link, .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: #e8505b;
  margin: 0;
  padding: 2rem;
  color: #FFFFFF;
  text-align: center;
}